import React from 'react'

class Logos extends React.Component {
  render() {
    var logos = [
      { 'name': 'Grounded - specialty coffee', 'image': './images/grounded.png' },
      { 'name': 'Solid Sounds - Soul DJ', 'image': './images/solidsounds.png'  },
      { 'name': 'Platform J - retro games enthusiast', 'image': './images/platformj.png'  },
      { 'name': 'Runaround - running shop', 'image': './images/runaround.png'  },
      { 'name': 'Open Source Tester - IT consultant', 'image': './images/ost.png'  },
      { 'name': 'Running Miss - Running Blogger', 'image': './images/runningmiss.png'  },
      { 'name': 'Pikalion - young football fan', 'image': './images/pikalion.png'  },
      { 'name': 'logopikto - brand logo mono', 'image': './images/piktobw.png'  },
      { 'name': 'logopikto - brand logo colour', 'image': './images/piktocol.png'  }
    ];

    return (
      <div className="pure-g">
        {logos.map(function(logo, index){
          return (
            <React.Fragment key={index}><div className="pure-u-sm-1 pure-u-md-1-2 pure-u-lg-1-3 logo-container">
              <div className="logo-wrapper">
                <div className="logo-small">
                  <img className="pure-img-responsive" src={require(`${logo.image}`)} alt={logo.name}></img>
                  <div className="after pure-u-1">{logo.name}</div>
                </div>
              </div>
            </div>
            </React.Fragment>)
        })}
      </div>
    )
  }
}

export default Logos;
